<template>
  <div>
    <div class="tool-bar">
      <div class="btn-group">
        <el-button-group>
          <el-button
            v-if="showBtn.isAdd"
            type="primary"
            size="small"
            @click="showProTaskForm(true)"
          >
            <i class="el-icon-plus"></i>
            
          </el-button>
          <el-button v-if="showBtn.isDel" type="danger" size="small" @click="del">
            <i class="el-icon-delete"></i>
            
          </el-button>
        </el-button-group>
      </div>
    </div>
    <el-table
      border
      ref="multipleTable"
      :data="taskList"
      tooltip-effect="dark"
      style="width: 100%"
      height="520"
      @selection-change="handleSelectionChange"
    >
      <el-table-column fixed align="center" type="selection" width="55"></el-table-column>
      <el-table-column align="center" prop="task_id" label="ID" width="55"></el-table-column>
      <el-table-column align="center" prop="start_time" label="" width="160"></el-table-column>
      <el-table-column align="center" prop="end_time" label="" width="160"></el-table-column>
      <el-table-column align="center" prop="major_name" label="" width="120"></el-table-column>
      <el-table-column align="center" prop="grade_name" label="" width="120"></el-table-column>
      <el-table-column align="center" prop="task_state" label="" width="120">
        <template
          slot-scope="scope"
        >{{+scope.row.task_state == 0 ? '' : +scope.row.task_state == 1 ? '' : ''}}</template>
      </el-table-column>
      <el-table-column align="center" prop="operation_username" label="" width="120"></el-table-column>
      <el-table-column align="center" prop="create_time" label="" width="160"></el-table-column>
      <el-table-column align="center" prop="update_time" label="" width="160"></el-table-column>
      <el-table-column fixed="right" align="center" label="" min-width="200">
        <template slot-scope="scope">
          <el-button
            v-if="showBtn.isEdit"
            @click="showProTaskForm(true, scope.row, $event)"
            type="success"
            size="mini"
          ></el-button>
          <el-button
            v-if="showBtn.isShowDetail"
            @click="toTaskDetail(scope.row)"
            type="primary"
            size="mini"
          ></el-button>
          <el-button
            v-if="!showBtn.isEdit && !showBtn.isShowDetail"
            type="info"
            size="mini"
            disabled
          ></el-button>
        </template>
      </el-table-column>
    </el-table>
    <div class="block">
      <el-pagination
        @size-change="handleSizeChange"
        @current-change="handleCurrentChange"
        :current-page="currentPage"
        :page-sizes="[1, 2, 3, 4]"
        :page-size="pageSize"
        :total="count"
        layout="total, sizes, prev, pager, next, jumper"
      ></el-pagination>
    </div>
    <proTaskForm
      :proTaskFormVisible="proTaskFormVisible"
      :proTaskForm="proTaskFormData"
      @close="showProTaskForm"
      :formTitle="formTitle"
    />
  </div>
</template>

<script>
import proTaskForm from "./proTaskForm";
import { mapState, mapMutations, mapActions } from "vuex";
export default {
  components: {
    proTaskForm
  },
  data() {
    return {
      proTaskFormVisible: false,
      proTaskFormData: {
        start_time: "",
        end_time: "",
        grade_name: "",
        major_name: ""
      },
      formTitle: "",
      taskModule: null,
      showBtn: {
        isAdd: false,
        isDel: false,
        isEdit: false,
        isShowDetail: false
      }
    };
  },
  created() {
    this.taskModule = this.showModuleList.filter(item => {
      if (item.name == "") {
        return true;
      } else {
        return false;
      }
    });
    if (this.taskModule[0].children) {
      this.taskModule[0].children.forEach(item => {
        switch (item.name) {
          case "":
            this.showBtn.isAdd = true;
            break;
          case "":
            this.showBtn.isDel = true;
            break;
          case "":
            this.showBtn.isEdit = true;
            break;
          case "":
            this.showBtn.isShowDetail = true;
            break;
          default:
            break;
        }
      });
    }
  },
  mounted() {
    this.findByPage({ page: this.currentPage, pageSize: this.pageSize });
    this.findGradeList({ page: 1, pageSize: 9999 });
    this.findMajorList({ page: 1, pageSize: 9999 });
  },
  computed: {
    ...mapState("task", [
      "multipleSelection",
      "currentPage",
      "pageSize",
      "count",
      "taskList"
    ]),
    ...mapState("role", ["showModuleList"])
  },
  methods: {
    ...mapMutations("task", ["setPage", "setPageSize", "setSelection"]),
    ...mapMutations("taskDetail", ["setTaskId"]),
    ...mapActions("task", ["findByPage", "delTasks"]),
    ...mapActions("grade", {
      findGradeList: "findByPage"
    }),
    ...mapActions("major", {
      findMajorList: "findByPage"
    }),
    handleSizeChange(val) {
      this.setPageSize(val);
    },
    handleCurrentChange(val) {
      this.setPage(val);
    },
    handleSelectionChange(val) {
      this.setSelection(val);
    },
    showProTaskForm(isShow, row, e) {
      this.proTaskFormVisible = isShow;
      e ? (this.formTitle = "") : (this.formTitle = "");
      if (row) {
        this.proTaskFormData = { ...row };
      }
    },
    // 
    toTaskDetail(row) {
      this.setTaskId(row.task_id);
      this.$emit("show", false);
    },
    del() {
      const length = this.multipleSelection.length;
      this.$confirm(`${length}?`, "", {
        confirmButtonText: "",
        cancelButtonText: "",
        type: "warning"
      })
        .then(() => {
          this.delTasks(this.multipleSelection).then(res => {
            this.$message({
              message: res.msg,
              type: "success",
              duration: 2000,
              center: true
            });
          });
        })
        .catch(() => {
          this.$message({
            type: "info",
            message: ""
          });
        });
    },
    handleClick(row) {
      this.editFormVisible = true;
      this.editFormData = { ...row };
    }
  }
};
</script>